.feed {
  padding: 69px 0 100px 0;

  background: linear-gradient(
      315.75deg,
      rgba(254, 210, 144, 0.7) 30.06%,
      #febdab 100.95%,
      rgba(254, 210, 144, 0.7) 106.36%
    ),
    linear-gradient(
      315.75deg,
      rgba(254, 189, 171, 0.74) 30.06%,
      rgba(243, 169, 248, 0.66) 50.74%,
      #e0d8f0 80.49%,
      #eaf7fe 127.9%,
      #eaf7fe 149.54%
    ),
    linear-gradient(
      315.75deg,
      rgba(254, 189, 171, 0.74) 30.06%,
      rgba(243, 169, 248, 0.66) 50.74%,
      #e0d8f0 80.49%,
      #eaf7fe 127.9%,
      #eaf7fe 149.54%
    );
  &__container {
    padding: 41px 0 59px 0;
    background: rgba(253, 253, 255, 0.52);
    border-radius: 20px;
  }

  &__title {
    @include font-setting(48px, 400, 58px);
    margin-left: -32px;
    text-align: center;
    color: $color-orange;
    padding-left: 99px;
    padding-right: 55px;
  }

  &__subtitle {
    @include font-setting(24px, 400, 29px);
    padding-left: 99px;
    padding-right: 55px;
    margin-top: 30px;
  }

  &__text {
    @include font-setting(16px, 500, 21px);
    padding-left: 99px;
    padding-right: 55px;

    &:nth-child(3) {
      margin-top: 40px;
    }

    &:nth-child(4) {
      margin-top: 10px;
      padding-right: 180px;
    }

    &_light {
      @include font-setting(16px, 400, 21px);
    }
  }

  &__subtext {
    text-align: center;
    @include font-setting(36px, 400, 46px);
    margin: 60px 0 40px 0;
  }

  &__block {
    margin: 0 auto;
    display: flex;
    max-width: 764px;
    gap: 30px;
  }

  &__card {
    width: 168px;
  }

  &__image {
    height: 168px;
    position: relative;
    border: 1px solid $color-orange;
    border-radius: 4px;
    background: $color-white;

    img {
      position: relative;
      display: block;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &__info {
    @include font-setting(16px, 400, 22px);
    margin: 30px 0 0 0;
  }

  &__arrow {
    position: relative;
    width: 69px;
    img {
      position: relative;
      top: 26%;
      left: 50%;
      transform: translatex(-50%);
    }
  }

  &__button {
    display: block;
    margin: 61px auto 0 auto;
  }

  &__link {
    color: $color-text;
    text-decoration: underline $color-orange;
  }
}
